import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Ann from './common'
import store from './store'
import i18n from './locale'
import { hasAuth, reRender } from './libs/utils'
import iView from 'view-design'
import './my-theme/index.less'
import Print from 'vue-print-nb'

Vue.prototype.bus = new Vue()
i18n.locale = store.state.language
// iview国际化
Vue.locale = () => { }
Object.defineProperties(Vue.prototype, {
    $hasAuth: {
        get () {
            return hasAuth
        }
    },
    $reRender: {
        get () {
            return reRender
        }
    },
    $baseUrl: {
        get () {
            return process.env.VUE_APP_API
        }
    },
    $getDictionary: {
        get () {
            const getDictionary = (key) => {
                if (!key) {
                    return
                }
                const localDictionary = window.localStorage.getItem('dictionary')
                if (localDictionary) {
                    return JSON.parse(localDictionary)[key] || []
                } else {
                    // window.location.href = `${window.location.origin}/login`
                }
            }
            return getDictionary
        }
    }
})

// 阻止启动生产消息
Vue.config.productionTip = false

// 捕获error
// eslint-disable-next-line
// Vue.config.errorHandler = function (err, vm, info) {
//     console.error(err)
// }

// 捕获warning
// eslint-disable-next-line
// Vue.config.warnHandler = function(msg, vm, trace) {
//     console.warn(`Warn: ${msg}\nTrace: ${trace}`)
// }

Vue.use(Ann)
Vue.use(iView, {
    // vue 跟 i18n之间的兼容问题
    i18n: function (path, options) {
        const value = i18n.t(path, options)
        if (value !== null && value !== undefined) {
            return value
        }
        return ''
    },
    // i18n: (key, value) => i18n.t(key, value),
    transfer: true
})
// Vue.use(VueWebsocket, 'ws://121.40.165.18:8800')

// export const SocketInstance = socketio('ws://121.40.165.18:8800');
// Vue.use(VueSocketIO, SocketInstance)

// 注册
Vue.use(Print)

new Vue({
    router,
    i18n,
    store,
    render: h => h(App)
}).$mount('#app')
